import { PAGE_SIZE, REFUSE } from '@/assets/config'
import { CODE, CODE_TREE, approvalKeys } from '@/assets/constant'
import Cache from '@/assets/cache'
import tabsService from '@/assets/template/tabs'
import utilizeService from '@/assets/template/utilize'
import dataService from '@/assets/method/data'
import Service from '@/assets/service/service'
export default {
  name: 'approval',
  data () {
    return {
      canRefuseAll: REFUSE,
      code: CODE.AcademicWorks,
      CodeTree: CODE_TREE,
      codeTree: [],
      canShow: false,
      items: [],
      total: 0,
      page: 1,
      pageSize: PAGE_SIZE,
      select: [0, 0, 0],
      condition: {},
      searchOptions: [],
      type: '',
      imgUrl: ''
    }
  },
  methods: {
    publicSearch (condition) {
      const data = this.codeTree[this.select[0]].children[this.select[1]]
      if (data.children) {
        this.type = data.children[this.select[2]].code
      } else {
        this.type = data.code
      }
      for (const key in condition) {
        this.condition[key] = condition[key]
      }
      this.getDataTables()
    },
    search () {
      this.getDataTables()
    },
    pageChange (page) {
      this.getDataTables(page)
    },
    getDataTables (page = 1) {
      const request = {
        type: this.type
      }
      if (page) {
        request['page'] = page
      }
      for (const key in this.condition) {
        if (this.condition[key]) {
          request[key] = this.condition[key]
        }
      }

      Service.Approval.get(request).then(response => {
        if (response.code == 200) {
          this.page = page
          this.items = response.data.data// 1.未审批 2.已驳回 3.审核成功
          this.total = response.data.total
          setTimeout(() => utilizeService.iCheck(), 200)
        }
      })
    },
    fileExport (request) {
      Service.FileDownload(request).then(response => {
        dataService.download(response)
      })
    },
    agreeSelect () {
      const ids = dataService.getChecked('select')
      if (ids.length == 0) {
        toastr.warning('请勾选选项')
        return
      }
      swal({
        title: '确定勾选选项批量审核通过吗',
        type: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#DD6B55',
        confirmButtonText: '确认',
        cancelButtonText: '取消',
        closeOnConfirm: false
      },() => {
        ids.map(id => {
          this.agreeConfirm(id)
        })
      })
    },
    agree (item) {
      swal({
        title: '确定审核通过吗',
        type: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#DD6B55',
        confirmButtonText: '确认',
        cancelButtonText: '取消',
        closeOnConfirm: false
      },() => {
        this.agreeConfirm(item.id)
      })
    },
    agreeConfirm(id) {
      Service.Approval.agree(id, { type: this.type }).then(response => {
        if (response.code == 200) {
          swal('审核成功！', '',  'success')
          this.getDataTables(this.page)
        }
      })
    },
    refuse (item) {
      const reason = prompt("确定审核驳回吗?");
      const isRefuse = reason != null
      if (!isRefuse) { // 如果取消驳回
        return
      }
      this.refuseConfirm(item.id, reason)
    },
    refuseConfirm (id, reason = '') {
      Service.Approval.refuse(id, {
        type: this.type,
        detail: reason
      }).then(response => {
        if (response.code == 200) {
          swal('驳回成功！', '',  'error')
          this.getDataTables(this.page)
        }
      })
    },
    refuseAll () {
      swal({
        title: '确定驳回当前分页所有申请吗?',
        type: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#DD6B55',
        confirmButtonText: '确认',
        cancelButtonText: '取消',
        closeOnConfirm: false
      },() => {
        this.items.map(item => {
          this.refuseConfirm(item.id)
        })
      })
    },
    details (item) {
      let url, name
      switch (this.type) {
        // 基本信息
        case CODE.Researcher:
          url = 'collegePersonnel/scientificResearchPersonnel'
          name = '科研人员'
          break
        case CODE.TalentPlan:
          url = 'collegePersonnel/talentPlan'
          name = '人才计划'
          break
        case CODE.AcademicCommittee:
          url = 'collegePersonnel/academicCommittee'
          name = '学术委员会'
          break
        case CODE.EthicsCommittee:
          url = 'collegePersonnel/ethicsCommittee'
          name = '伦理委员会'
          break
        case CODE.CollegeArea:
          url = 'collegeFacilities/area'
          name = '实验场地'
          break
        case CODE.CollegeDevice:
          url = 'collegeFacilities/device'
          name = '研究设备'
          break
        // 学术成果
        case CODE.AcademicWorks:
          url = 'academicWorks'
          name = '学术著作'
          break
        case CODE.PeriodicalPaperChinese:
          url = 'periodicalPaper/periodicalPaperChinese'
          name = '期刊论文-中文'
          break
        case CODE.PeriodicalPaperEnglish:
          url = 'periodicalPaper/periodicalPaperEnglish'
          name = '期刊论文-英文'
          break
        case CODE.ResearchProjectsCountry:
          url = 'researchProjects/country'
          name = '科研项目(国家级项目)'
          break
        case CODE.ResearchProjectsProvince:
          url = 'researchProjects/province'
          name = '科研项目(省部级项目)'
          break
        case CODE.ResearchProjectsOther:
          url = 'researchProjects/other'
          name = '科研项目(其他纵向项目)'
          break
        case CODE.ResearchProjectsLandscape:
          url = 'researchProjects/landscape'
          name = '科研项目(横向项目)'
          break
        case CODE.ResearchAwards:
          url = 'researchAwards'
          name = '科研获奖'
          break
        case CODE.Patent:
          url = 'patent'
          name = '发明专利'
          break
        case CODE.MeetingPaper:
          url = 'meetingPaper'
          name = '会议论文'
          break
        case CODE.ResearchReport:
          url = 'researchReport'
          name = '研究报告'
          break
        case CODE.HostMeeting:
          url = 'hostMeeting'
          name = '主办会议/培训'
          break
        case CODE.ParticipateMeeting:
          url = 'participateMeeting'
          name = '参与会议/培训'
          break
        case CODE.Score:
          url = 'score'
          name = '评分'
          break
        // 人才管理
        case CODE.MasterTraining:
          url = 'masterTraining'
          name = '硕士培养'
          break
        case CODE.DoctorTraining:
          url = 'doctorTraining'
          name = '博士培养'
          break
        case CODE.TeacherAward:
          url = 'teacherAward'
          name = '教学获奖'
          break
        case CODE.GoodCourse:
          url = 'goodCourse'
          name = '精品课程'
          break
        case CODE.TeachingMaterialConstruction:
          url = 'teachingMaterialConstruction'
          name = '教材建设'
          break
        case CODE.StudentAwards:
          url = 'studentAwards'
          name = '学生获奖情况'
          break
        case CODE.StudentWorks:
          url = 'studentWorks'
          name = '学生发表著作情况'
          break
        case CODE.StudentMeeting:
          url = 'studentMeeting'
          name = '学生境内/境外参加会议'
          break
        case CODE.StudentStudy:
          url = 'studentStudy'
          name = '学生境内/境外交流学习情况'
          break
        case CODE.StudentMeetingPaper:
          url = 'studentMeetingPaper'
          name = '学生发表会议论文'
          break
        case CODE.PracticeBase:
          url = 'practiceBase'
          name = '实践基地'
          break
        case CODE.PartTimeTutor:
          url = 'partTimeTutor'
          name = '兼职导师'
          break
        case CODE.ParticipateProfessionalPractice:
          url = 'participateProfessionalPractice'
          name = '参加专业实践活动'
          break
        case CODE.GraduateEnrollment:
          url = 'graduateEnrollment'
          name = '招生与毕业情况'
          break

        case CODE.RulesRegulations:
          url = 'rules'
          name = '规则制度'
          break
        default:
          alert('请配置url')
          return
          break;
      }
      tabsService.addDetailsTab('/views/' + url + '/' + item.id, name + '-详情-' + item.id)
    }
  },
  created () {
    Service.PageSet.get().then(response => {
      if (response.code == 200) {
        const result = response.data
        this.codeTree = this.CodeTree.filter(option => {
          if (result[option.model] == 1 && Cache.hasPermission (option.permission)) {
            return option
          }
        })
      } else {
        this.codeTree = this.CodeTree
      }
      utilizeService.iCheck()
      this.canShow = true
    })

  },
  mounted () {

  }
}



